.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #c2c2c2;

  font-weight: 600;
  text-transform: uppercase;

  overflow: auto; /* to scroll the menu if needed */

  &__top {
    top: 0;
    z-index: 1;
    margin-bottom: 24px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $menu-text-color;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
